<template>
  <div>
      <el-dialog title="" :visible.sync="delPushDialog.delPush" center width="450px" :close-on-click-modal="false">
      <div>
        <div class="ui-com-center">
          <span>K3</span>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelPush()"> </el-button>
        <el-button type="danger" @click="confirmUnPushReceipt()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
    export default {
        data () {
            return {

            };
        },
        props: ['delpushdialog'],
        computed: {
            delPushDialog () {
                return this.delpushdialog;
            }
        },
        methods: {
            confirmUnPushReceipt () {
                this.$emit('confirmDelPush');
            },
            cancelPush () {
                this.$emit('cancelDelPush');
            }
        }
    };
</script>



