<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <span></span>
                        <el-select
                                size="medium"
                                v-model="queryData.docName"
                                placeholder="">
                            <el-option
                                    v-for="item in optionList.orderTypeList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                    <li>
                        <span></span>
                        <el-date-picker
                                size="medium"
                                popper-class="ui-layout_date"
                                v-model="queryData.account"
                                type="month"
                                format="yyyy  MM "
                                placeholder=""
                                :editable="false"
                                :clearable="false"
                                range-separator=""
                                value-format="yyyy-MM"
                                @focus="handleFocusDateInput">
                        </el-date-picker>
                    </li>
                    <li>
                        <span></span>
                        <el-select
                                size="medium"
                                v-model="queryData.sum"
                                placeholder="">
                            <el-option
                                    v-for="item in optionList.summaryStateList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                    <li>
                        <span></span>
                        <el-select
                                size="medium"
                                v-model="queryData.associate"
                                placeholder="">
                            <el-option
                                    v-for="item in optionList.associateStateList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                    <li class="ui-main-search-btn">
                        <el-button @click="handleResetQueryInfo()" size="medium"></el-button>
                        <el-button size="medium" type="primary" @click="handleQueryInfo({limit: pageData.currentList})"></el-button>
                    </li>
                </ul>
            </div>
        </transition>
        <div class="ui-main-module-table" v-if="initTable">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'drop_down_2' : 'drop_down_1'"></ComIcon></span>
            <el-table
                    ref="multipleSelection"
                    :data="tableData"
                    class="ui-data-height"
                    border
                    stripe
                    style="width: 100%"
                    :height="tableHeight">
                <el-table-column
                        type="index"
                        :label="tableKey[0]"
                        :index="autoAddTableInx"
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="order_type_name"
                        :label="tableKey[1]"
                        width="200">
                </el-table-column>
                <el-table-column
                        prop="account_time"
                        :label="tableKey[2]"
                        width="180">
                    <template slot-scope="scope">
                        <span>{{scope.row.account_time_cn}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="account_duration"
                        :label="tableKey[3]"
                        width="200">
                </el-table-column>
                <el-table-column
                        prop="close_status"
                        :label="tableKey[4]">
                    <template slot-scope="scope">
                        <span :class="'ui-state-color' + scope.row.is_close">{{scope.row.close_status}}</span>
                    </template>

                </el-table-column>
                <el-table-column
                        prop="summary_status_cn"
                        :label="tableKey[5]">
                    <template slot-scope="scope">
                        <span :class="'ui-txt-color' + scope.row.summary_status">{{scope.row.summary_status_cn}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="begin_time"
                        :label="tableKey[6]"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="end_time"
                        :label="tableKey[7]"
                        width="180">
                </el-table-column>
                <el-table-column   prop="operator"  :label="tableKey[8]">
                    <template slot-scope="scope">
                        <el-button
                                type="primary"
                                size="medium"
                                :disabled="buttonList[0].permit(thisVm) || !(scope.row.summary_status === '0' && scope.row.is_close === '1')"
                                plain
                                @click="handleSumInfo(scope.row)">
                        </el-button>
                    </template>
                </el-table-column>
                <template slot="empty">
                    <div>
                        <NotData v-show="isNotData"></NotData>
                    </div>
                </template>
            </el-table>
        </div>
        <div class="ui-main-module-page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import * as storage from '@/utils/storage'
    import {
        mapGetters,
        mapActions
    } from 'vuex'
    import config from '@/assets/js/config';
    import {
        getSummaryInfo,
        getSummaryData
    } from '../../../api/operate_management/operate_sum';
    import {
        autoAddTableInx
    } from '@/services/index'

    export default {
        data() {
            return {

                // 
                isNotData: false,

                // 
                thisVm: {},

                //
                showSearch: true,

                initTable: false,
                tableHeight: 100,

                // 
                optionList: {
                    orderTypeList: [
                        {value: "0", label: ""},
                        {value: "1", label: ""},
                        {value: "2", label: ""},
                        {value: "3", label: ""},
                        {value: "4", label: ""},
                        {value: "5", label: ""},
                        {value: "6", label: ""}
                    ],
                    summaryStateList: [
                        {value: "", label: ""},
                        {value: "0", label: ""},
                        {value: "1", label: ""},
                        {value: "2", label: ""},
                        {value: "3", label: ""}
                    ],
                    associateStateList: [
                        {value: "", label: ""},
                        {value: "0", label: ""},
                        {value: "1", label: ""},
                    ],
                },

                // 
                queryData: {
                    docName:'',
                    account:'',
                    sum:'',
                    associate:'0'
                },

                // 
                tableKey: ["","","","","","","","",""],
                tableData: [],

                //
                countData:{},

                //
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                }
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'summaryInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetSummaryInfo'
            ]),
            getSummaryInfo,
            getSummaryData,
            autoAddTableInx,
            handleQueryInfo(params, isFn) {

                // 
                if (isFn) {
                    // 
                    this.queryData = JSON.parse(this.queryParamsInfo['operate_summary']);
                }

                let obj = {
                    order_type_name: this.queryData.docName,
                    account_time: !this.queryData.account ? this.queryData.account : this.queryData.account + '-01 00:00:00',   // 
                    close_status: this.queryData.associate,
                    summary_status: this.queryData.sum
                };

                // 
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'operate_summary',
                    value: JSON.stringify(this.queryData)
                });

                obj = Object.assign(params || {}, obj);

                this.GetSummaryInfo(obj).then(({data}) => {

                    // 
                    if (data.status === 2) return;
                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },

            //
            handleSumInfo(row){
                //
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type:'warning',
                }).then(() => {
                    let obj = {
                        bill_type: row.order_type,
                        id: row.id,
                        operator: JSON.parse(storage.getSessionStorage('userInfo')).uid
                    };
                    this.getSummaryData(obj).then(({ data })=>{

                        if (data.status) {
                            this.$message({
                                type: 'success',
                                message: ''
                            });
                            this.handleQueryInfo({limit: this.pageData.currentList});
                        } else {
                            this.$message({
                                type: 'error',
                                message: data.errorMess || ''
                            });
                        }
                    });
                    }).catch(err => {
                        return err;
                    });
            },

            //
            handleResetQueryInfo: function () {
                this.queryData = {
                    docName:'',
                    account:'',
                    sum:'',
                    associate:'0'
                };
            },
            handleSizeChange: function (size) {
                this.handleQueryInfo({
                    limit: size,
                    offset: 1
                }, true)
            },
            handleCurrentChange: function (idx) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: idx
                }, true)
            },

            // 
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1
                }, true);
            },

            // 
            handleFocusDateInput() {
                clearInterval(timeout);
                let timeout = setTimeout(() => {
                    let dateEl = document.querySelectorAll('.ui-layout_date .el-month-table td a');
                    dateEl.forEach((e, i) => {
                        e.innerText = e.innerText.replace('', '')
                    })
                }, 100)
            },

            // 
            handleQueryInfoCallBack(data) {

                // 
                this.isNotData = !data.data_list.value.length;

                // 
                this.tableKey = data.data_list.key;
                this.tableData = data.data_list.value;

                for (let i = 0; i < this.tableData.length; i++) {
                    let str = this.tableData[i].account_time;
                    this.tableData[i].account_time_cn = str.substring(0, str.indexOf(' ')).replace('-', '').replace('-01', '');
                }

                this.optionList.summaryStateList = data.data_list.summary_status;
                this.optionList.associateStateList = data.data_list.close_status;
                this.optionList.orderTypeList = data.data_list.order_type_cn;

                //
                this.pageData.currentList = data.page_data.limit;
                this.pageData.currentPage = data.page_data.offset;
                this.pageData.currentTotal = data.page_data.total;
            }
        },
        created: function () {
            this.thisVm = this;
            try {
                if (JSON.stringify(this.summaryInfo) === '{}') {
                    this.handleQueryInfo();
                } else {
                    this.queryData = JSON.parse(this.queryParamsInfo['operate_summary']);
                    this.handleQueryInfoCallBack(this.summaryInfo);
                }
            } catch (e) {
                console.log(e);
            }
        },
        mounted: function () {

            this.tableHeight = config.setTableHeight('ui-main-module');
            this.initTable = true;
        },
        watch: {
            showSearch(val, old) {
                let vm = this;
                document.querySelector('body').style.overflow = 'hidden';
                setTimeout(() => {
                    vm.initTable = false;
                    vm.tableHeight = config.setTableHeight('ui-main-module');
                    vm.initTable = true;
                    document.querySelector('body').style.overflow = '';
                }, 400)
            }
        }
    };
</script>

<style lang="less" scoped>

    @import (reference) '../../../assets/less/index';
</style>


