                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="2-2"> <i :class=" navIndex === '2-2' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="2-3"> <i :class=" navIndex === '2-3' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="2-4"> <i :class=" navIndex === '2-4' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="2-5"> <i :class=" navIndex === '2-5' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="3">
                <template slot="title">
                    <ComIcon :name="!!/^3/.exec(navIndex) ? 'icon4_1' : 'icon4'"></ComIcon>
                    <span :class="!!/^3/.exec(navIndex) ? 'active' : 'default'"></span>
                </template>
                <el-menu-item-group>
                    <el-menu-item index="3-1"> <i :class=" navIndex === '3-1' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-submenu index="3-2">
                        <template slot="title"></template>
                        <el-menu-item index="3-2-1"><i :class=" navIndex === '3-2-1' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                        <el-menu-item index="3-2-2"><i :class=" navIndex === '3-2-2' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                    </el-submenu>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="3-3"> <i :class=" navIndex === '3-3' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="3-4"> <i :class=" navIndex === '3-4' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="3-5"> <i :class=" navIndex === '3-5' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="3-6"> <i :class=" navIndex === '3-6' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="4">
                <template slot="title">
                    <ComIcon :name="!!/^4/.exec(navIndex) ? 'icon7_1' : 'icon7'"></ComIcon>
                    <span :class="!!/^4/.exec(navIndex) ? 'active' : 'default'"></span>
                </template>
                <!--<el-menu-item-group>-->
                    <!--<el-menu-item index="4-1"></el-menu-item>-->
                <!--</el-menu-item-group>-->
                <el-menu-item-group>
                    <el-menu-item index="4-2"> <i :class=" navIndex === '4-2' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="4-3"> <i :class=" navIndex === '4-3' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="4-4"> <i :class=" navIndex === '4-4' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="4-5"> <i :class=" navIndex === '4-5' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="5">
                <template slot="title">
                    <ComIcon :name="!!/^5/.exec(navIndex) ? 'icon8_1' : 'icon8'"></ComIcon>
                    <span :class="!!/^5/.exec(navIndex) ? 'active' : 'default'"></span>
                </template>
                <el-menu-item-group>
                    <el-menu-item index="5-1"> <i :class=" navIndex === '5-1' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
