                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="5-3"> <i :class=" navIndex === '5-3' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="5-4"> <i :class=" navIndex === '5-4' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="5-5"> <i :class=" navIndex === '5-5' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="5-6"> <i :class=" navIndex === '5-6' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="5-7"> <i :class=" navIndex === '5-7' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="5-8"> <i :class=" navIndex === '5-8' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="6">
                <template slot="title">
                    <ComIcon :name="!!/^6/.exec(navIndex) ? 'icon9_1' : 'icon9'"></ComIcon>
                    <span :class="!!/^6/.exec(navIndex) ? 'active' : 'default'"></span>
                </template>
                <el-menu-item-group>
                    <el-submenu index="6-1">
                        <template slot="title"></template>
                        <el-menu-item index="6-1-1"></el-menu-item>
                        <el-menu-item index="6-1-2"></el-menu-item>
                    </el-submenu>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="6-2"> <i :class=" navIndex === '6-2' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <!--<el-menu-item-group>-->
                    <!--<el-menu-item index="6-3"> <i :class=" navIndex === '6-3' ? 'el-icon-caret-left' : ''"></i></el-menu-item>-->
                <!--</el-menu-item-group>-->
            </el-submenu>
            <el-submenu index="7">
                <template slot="title">
                    <ComIcon :name="!!/^7/.exec(navIndex) ? 'icon10_1' : 'icon10'"></ComIcon>
                    <span :class="!!/^7/.exec(navIndex) ? 'active' : 'default'"></span>
                </template>
                <el-menu-item-group>
                    <el-menu-item index="7-1"> <i :class=" navIndex === '7-1' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <!--<el-menu-item-group>-->
                    <!--<el-menu-item index="7-2"> <i :class=" navIndex === '7-2' ? 'el-icon-caret-left' : ''"></i></el-menu-item>-->
                <!--</el-menu-item-group>-->
            </el-submenu>
        </el-menu>
    </div>
</template>

<script>
    import * as storage from '@/utils/storage'
    import { mapGetters, mapActions } from 'vuex';
    import {
        submitCloseAccount,
        submitUnCloseAccount
    } from "@/api/login";
    import routerConfig from '@/router/routerConfig';
    import * as service from '@/services';
    import CONFIG from '@/assets/js/config';

    export default {
        name: "NavPage",
        data() {
            return {
                isShowUpdate: false,
                isCollapse: false,
                activeIndex: '1-1',
                openIndex: '',
                navFnConfig: {

                    // 
                    '6-1-1': 'handleCloseAccount',

                    // 
                    '6-1-2': 'handleUnCloseAccount'
                },
                closeAccountData: {
                    operator: '',
                    date: ''
                }
            }
        },
        computed: {
            ...mapGetters(['permitPath', 'navIndex'])
        },
        methods: {
            submitCloseAccount,
            submitUnCloseAccount,
            handleSelectModule(inx) {

                if (this.navFnConfig[inx]) {
                    if (this.permitPath.indexOf(routerConfig.navJumpFn(inx).path) === -1) {
                        this.$message({
                            type: 'warning',
                            message: ''
                        });
                        return;
                    }

                    this[this.navFnConfig[inx]]();
                    return;
                }
                this.activeIndex = inx;
                storage.setSessionStorage('navActive', inx);

                let to = routerConfig.navJumpFn(inx);
                this.$router.push(to.path);
                this.$store.commit('SAVE_TAB_LABELS', {
                    path: to.path,
                    name: to.name,
                    index: inx
                });
                this.$store.commit('SAVE_NAV_INDEX', inx)
            },

            // 
            handleCloseAccount() {
                this.submitCloseAccount().then(({ data }) => {
                    this.closeAccountData.date = data.datetime.unclose_time;

                    let dateStr = this.closeAccountData.date;
                    let showDate = parseInt(dateStr.substring(dateStr.indexOf('-') + 1, dateStr.indexOf('-') + 3));

                    const h = this.$createElement;
                    this.$msgbox({
                        title: '',
                        message: h('p', null, [
                            h('p', null, `${showDate}(${showDate})?`),
                            h('span', null, ``)
                        ]),
                        showCancelButton: true,
                        confirmButtonText: '',
                        cancelButtonText: '',
                    }).then(action => {
                        let obj = {
                            operator: JSON.parse(storage.getSessionStorage('userInfo')).uid,
                            datetime: this.closeAccountData.date,
                            is_closed: 1
                        };
                        this.submitCloseAccount(obj).then(({ data }) => {
                            if (data.status) {
                                this.$message({
                                    type: 'success',
                                    message: ''
                                })
                            } else {
                                this.$message({
                                    type: 'error',
                                    message: data.erorrMess || ''
                                })
                            }
                        })
                    });
                })
            },

            // 
            handleUnCloseAccount() {

                this.submitUnCloseAccount().then(({ data }) => {
                    this.closeAccountData.date = data.datetime.close_time;

                    let dateStr = this.closeAccountData.date;
                    let showDate = parseInt(dateStr.substring(dateStr.indexOf('-') + 1, dateStr.indexOf('-') + 3));

                    const h = this.$createElement;
                    this.$msgbox({
                        title: '',
                        message: h('p', null, [
                            h('p', null, `${showDate}(${showDate})?`),
                            h('span', { style: 'color: red' }, ':'),
                            h('span', null, `${showDate}`)
                        ]),
                        showCancelButton: true,
                        confirmButtonText: '',
                        cancelButtonText: '',
                    }).then(action => {
                        let obj = {
                            modify_user: JSON.parse(storage.getSessionStorage('userInfo')).uid,
                            datetime: this.closeAccountData.date,
                            is_unclose: 1
                        };
                        this.submitUnCloseAccount(obj).then(({ data }) => {
                            if (data.status) {
                                this.$message({
                                    type: 'success',
                                    message: ''
                                })
                            } else {
                                this.$message({
                                    type: 'error',
                                    message: data.errorMess || ''
                                })
                            }
                        })
                    });
                })
            }
        },
        created() {
            if (process.env.NODE_ENV == 'production') {
                window.parent.postMessage({
                    complete: true
                }, CONFIG.IN_PRODUCTION_PATH)
            } else {
                window.parent.postMessage({
                    complete: true
                }, CONFIG.IN_DEVELOPMENT_PATH)
            }
        },
        watch: {
            isCollapse(val) {
                let view = document.querySelector('.ui-content');
                if (val) {
                    setTimeout(() => {
                        view.style.paddingLeft = '90px';
                    }, 300)
                } else {
                    // this.activeIndex = '1-2';
                    this.$refs.navMenu.updateActiveIndex();
                    view.style.paddingLeft = '220px';
                }
            },
            navIndex(val) {
                this.activeIndex = val;
                this.$refs.navMenu.updateActiveIndex();
            }
        }
    }
</script>

<style scoped lang="less">
    @import (reference) '../../assets/less/index';
    .ui-nav-bar {
        background-color: @base-nav-bgColor;

        .ui-logo {
            float: left;
            margin-left: 26px;
            background-color: @base-nav-bgColor;

            img {
                margin-top: 20px;
            }
        }
    }

    .active {
        color: @base-nav-activeColor;
    }

    .default {
        color: @base-default-color;
    }
</style>


